import { BrowserRouter as Router, Link, Switch,Route } from 'react-router-dom';
import UserDetails from './UserDetails';
import AdminHome from './AdminHome';

function AdminNavbar()
{
      return (
        <Router>
        <nav className="navbar navbar-expand-lg navbar-light shadow-5">
          <div className="collapse navbar-collapse" id="navbarText">
            <h5 className="mr-5 mt-1">Admin Panel</h5>
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link to="/" className="nav-link"><i class="fas fa-home"></i> Home</Link>
              </li>
              <li className="nav-item">
                <Link to="/users" className="nav-link"><i class="fas fa-user"></i> Users</Link>
              </li>
              
            </ul>
          </div>
        </nav>
        <Switch>
          <Route exact component={AdminHome} path="/" />
          <Route exact component={UserDetails} path="/users" />
        </Switch>
      </Router>
      )
}

export default AdminNavbar;